#footer-block {
    background-color: #222222;
    min-height: 300px;
    padding-left: 150px;
    padding-right: 150px;
}

#navigation-bar {
    background-color: aqua;
}

.main-content {
    background-color: coral;
    min-height: 1700px;
}

.social-media-icon {
    height: 32px;
    width: 32px;
}

#map-office-label {
    font-size: 20px;
    color: red;
}

.sticky {
    position: fixed;
    top: 0;
    width: 100%;
}

.footer-nav-element {
    height: 20px !important;
    min-height: 25px !important;
    float: none !important;
}

.footer-nav-element-selected {
    height: 20px !important;
    padding-top: 3px !important;
    min-height: 25px !important;
    min-width: 100px !important;
}
